// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueI18n from 'vue-i18n'
import * as filters from './filters/filters.js'


import LangEn from '../static/lang/en'
import LangZhCHS from '../static/lang/zhCHS'
import LangZhCHT from '../static/lang/zhCHT'


import store from "./store";

import Vant from 'vant';
import 'vant/lib/index.css';

  


import VueClipboard from 'vue-clipboard2';

Vue.use(VueClipboard)
Vue.use(Vant);
Vue.use(Vant);
Vue.prototype.ArrList={
    oldArr:"0xf99c112f61f4926240d55319cbee85703b577190",
    dappArr:"0xc5fe45aa146345025de5b0268dcdbae28b0c3986",
    UsdtArr:"0xd79a8a420c9C01Ed3290dcB75335297Dbf3B3c6F",
    FiveArr:"0xb5d57B6c9394106aBdf66cc1d9e6AF18D5a26944",//gns
    BuyArr:"0x56c361232095d1223c0258fb213def1354ab3964",//buy
    XhArr:"0x000000000000000000000000000000000000dEaD",
    AesLpArr:"0xe068cc60fd1c4b13bcd6b39de69158b23f22c966",//NFT
    ListArr:"0x3dbe881138e5b1b60122530eb1f6c5504960ea95",//
    tomoArr:"0xd7dab63b2f4ffbfc46f976d4e8a1e6c7004e980c",
    // 0xC5717beb3d8eda80Fbd91EC50e7F1643Fc981df3
}

Vue.config.productionTip = false;

Object.keys(filters).forEach(key=>{
    Vue.filter(key,filters[key])//
})

Vue.use(VueI18n);



const i18n = new VueI18n({
    locale: 'zhCHS', // 
    messages: {
        'en': LangEn,
        'zhCHS': LangZhCHS,
        'zhCHT': LangZhCHT
    }
});



/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n,
    template: '<App/>',
    components: { App }
});


