    line-height: 26px;
  }

  .tipTxt {
    text-align: center;
    margin: 15px 0 0;
  }

  .btn {
    width: 80%;
    margin: 20px auto 20px;
    display: block;
    background: #0627FF;
    border: none;
    text-align: center;
    color: #fff;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    font-size: 17px;
    border-radius: 10px;
    padding: 15px 0;
  }
}

.btnB {
  position: fixed;
  bottom: 0;
  background: #0627FF;
  width: 100%;
  z-index: 3;
  padding: 10px 0;
  text-align: center;
  font-size: 16px;
  color: #fff;
}
.ban{
  position: relative;
  text-align: center;
  img{
    position: relative;
  }
 a{
  position: absolute;
  animation: my 2s infinite;
  display: block;
  color: #fff;
  background: linear-gradient(#8c31ff, #3f5cea);
    width: 120px;
    text-align: center;
    padding: 5px 10px;
    border-radius: 30px;
    font-size: 16px;
    bottom:12px;
    left: 70px;
 }
}
@keyframes my {
  0% {
    transform: translate(0px , 0px);
    /*0*/
  }

  50% {
    transform: translate(-10px,0px );
    /*360*/
  }
  100% {
    transform: translate(0px , 0px);
    /*0*/
  }
}

